import React from "react";
import Page from "../../Components/Page";
import Icon from "@mdi/react";
import "./HomePage.css";
import {
  mdiMapMarker,
  mdiPhone,
  mdiInstagram,
  mdiGithub,
  mdiGitlab,
  mdiWhatsapp,
  mdiStackOverflow,
} from "@mdi/js";
export default function HomePage() {
  return (
    <Page>
      <div className="HomePage row fix-distance">
        <div className="col-md-12 col-sm-12 set-ele-middle flex-column">
        <div className="profile-pic-container set-ele-middle">
            <div className="wavy-circle"></div>
            <svg className="wavy-svg">
              <filter id="wavy">
                <feTurbulence
                  x="0"
                  y="0"
                  baseFrequency="0.009"
                  numOctaves="5"
                  seed="2"
                >
                  <animate
                    attributeName="baseFrequency"
                    dur="60s"
                    values="0.02;0.005;0.02"
                    repeatCount="indefinite"
                  />
                </feTurbulence>
                <feDisplacementMap in="SourceGraphic" scale="30" />
              </filter>
            </svg>
            <img
              className="profile-pic rounded-circle"
              src="./assets/images/hero.png"
              alt="profile"
              width="60%"
            />
          </div>
          <div className="neon-font">
            <b>
              Amir<span>Hossein </span>Goo<span>darzi</span>
            </b>
          </div>
          <h2 className="my-responsibility">
            <span>Web Designer</span> | <span>Developer</span> |{" "}
            <span>Programmer</span>
          </h2>
          <div className="row my-4">
            <div className="col-sm-12 col-md-5">
              <ul className="">
                <li style={{ color: "#fee" }}>
                  AmirGoodarzi.web@gmail.com
                </li>
                <li style={{ color: "#fee" }}>
                  <span className="mx-2">
                    <Icon path={mdiPhone} size={1} />
                  </span>
                  +98 905 632 5077
                </li>
                <li style={{ color: "#fee" }}>
                  <span className="mx-2">
                    <Icon path={mdiMapMarker} size={1} />
                  </span>
                  iran , Tehran
                </li>
              </ul>
            </div>
            <div className="col-sm-12 col-md-7 set-ele-middle">
              <a
                className="NeonBtn"
                href="./assets/files/ResumeFile.pdf"
                target="_blank"
              >
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                Downlaod CV
              </a>
            </div>
          </div>

          <ul className="social-media">
            <li>
              <a href="https://instagram.com/Sektowr" target="_blank" rel="noopener noreferrer">
                <Icon path={mdiInstagram} size={1} color="#11b1d5" />
              </a>
            </li>
            <li>
              <a href="https://github.com/AmirHosseinGoodarzi" target="_blank" rel="noopener noreferrer">
                <Icon path={mdiGithub} size={1} color="#11b1d5" />
              </a>
            </li>
            <li>
              <a href="https://gitlab.com/Sektowr" target="_blank" rel="noopener noreferrer">
                <Icon path={mdiGitlab} size={1} color="#11b1d5" />
              </a>
            </li>
            <li>
              <a href="https://wa.me/989167943707" target="_blank" rel="noopener noreferrer">
                <Icon path={mdiWhatsapp} size={1} color="#11b1d5" />
              </a>
            </li>
            <li>
              <a href="/" target="_blank" rel="noopener noreferrer">
                <Icon path={mdiStackOverflow} size={1} color="#11b1d5" />
              </a>
            </li>
          </ul>
        </div>
      </div>
    </Page>
  );
}
