import React from "react";
import Page from "../../Components/Page";
import ProgressBar from "../../Components/ProgressBar";
import CircleProgress from "../../Components/CircleProgress";
import "./Skills.css";
export default function Skills() {
  return (
    <Page>
      <div className="skills fix-distance">
        <div className="neon-font">
          <b>
            <span>My Skills</span>
          </b>
        </div>
        <br />
        <br />
        <div className="row">
          <div className="col-md-6 col-sm-12 set-ele-middle flex-column mb-5">
            <h2>Technical Skills</h2>
            <br />
            <br />
            <ProgressBar
              progressName="WebBase(Html/Css/Js)"
              progressPersent="90%"
            />
            <ProgressBar progressName="jQuery" progressPersent="80%" />
            <ProgressBar progressName="BootStrap v4" progressPersent="90%" />
            <ProgressBar progressName="React js" progressPersent="90%" />
            <ProgressBar progressName="wordpress" progressPersent="85%" />
            <ProgressBar progressName="Git" progressPersent="30%" />
            <ProgressBar progressName="PHP" progressPersent="90%" />
            <ProgressBar progressName="MySql" progressPersent="80%" />
            <ProgressBar
              progressName="Laravel (Lerning)"
              progressPersent="30%"
            />
          </div>
          <div className="col-md-6 col-sm-12">
            <h2>Professional Skills</h2>
            <br />
            <br />
            <div className="row">
              <CircleProgress end="80" text="Communication" />
              <CircleProgress end="50" text="Team Work" />
              <CircleProgress end="50" text="Project Management" />
              <CircleProgress end="70" text="Creativity" />
            </div>
            <div className="row set-ele-middle humberger-image">
              <img src="./assets/images/humber.png" alt="humber" width="60%" />
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
}
