import React from "react";
import "./CircleProgress.css";
export default function CircleProgress(props) {
  if (props.end === "50") {
    return (
      <div className="col-sm-6 col-md-4 set-ele-middle flex-column my-2">
        <div className="progress-circle over50 p50">
          <span>{props.end + "%"}</span>
          <div className="left-half-clipper">
            <div className="first50-bar"></div>
            <div className="value-bar"></div>
          </div>
        </div>
        <h6 className="text-center">{props.text}</h6>
      </div>
    );
  } else if (props.end === "70") {
    return (
      <div className="col-sm-6 col-md-4 set-ele-middle flex-column my-2">
        <div className="progress-circle over50 p70">
          <span>{props.end + "%"}</span>
          <div className="left-half-clipper">
            <div className="first50-bar"></div>
            <div className="value-bar"></div>
          </div>
        </div>
        <h6 className="text-center">{props.text}</h6>
      </div>
    );
  } else if (props.end === "80") {
    return (
      <div className="col-sm-6 col-md-4 set-ele-middle flex-column my-2">
        <div className="progress-circle over50 p80">
          <span>{props.end + "%"}</span>
          <div className="left-half-clipper">
            <div className="first50-bar"></div>
            <div className="value-bar"></div>
          </div>
        </div>
        <h6 className="text-center">{props.text}</h6>
      </div>
    );
  }
}
