import React from "react";

export default function HexagonPortfolioCard(props) {
  return (
    <>
      <li className="hex">
        <div className="hexIn">
          <a
            className="hexLink"
            data-toggle="modal"
            data-target={props.modalID}
            href="##"
          >
            <img src={props.imgSrc} alt="Work" />
            <h1>{props.title}</h1>
            <p>{props.text}</p> 
          </a>
        </div>
      </li>
    </>
  );
}
