import React from "react";
import { NavLink } from "react-router-dom";
import Functions from "./Functions";

export default function Page(props) {
  return (
    <div className="navbar-container">
      <Functions />
      <div className="pre-loader set-ele-middle">
        <div className="loader">
          <span>
            <i>L</i>
          </span>
          <span>
            <i>O</i>
          </span>
          <span>
            <i>A</i>
          </span>
          <span>
            <i>D</i>
          </span>
          <span>
            <i>I</i>
          </span>
          <span>
            <i>N</i>
          </span>
          <span>
            <i>G</i>
          </span>
          <span>
            <i>.</i>
          </span>
        </div>
      </div>

      <div className="navbar">
        <div className="menu">
          <h3 className="neon-font logo">
            <b>
              Person<span>al </span>Res<span>ume</span>
            </b>
          </h3>
          <div className="hamburger-menu">
            <div className="bar"></div>
          </div>
        </div>
      </div>
      <div className="main-navbar-container">
        <div className="navbar-main">
          <header className="navbar-header">
            <div className="navbar-overlay">
              <div className="container-fluid navbar-inner">
                {props.children}
              </div>
            </div>
          </header>
        </div>
        <div className="shadow one"></div>
        <div className="shadow two"></div>
      </div>
      <div className="navbar-links text-center">
        <ul>
          <li>
            <NavLink to="/">Home</NavLink>
          </li>
          <li>
            <NavLink to="/AboutMe">About Me</NavLink>
          </li>
          <li>
            <NavLink to="/Skills">Skills</NavLink>
          </li>

          <li>
            <NavLink to="/Expriences">Expriences</NavLink>
          </li>
          <li>
            <NavLink to="/Portfolio">Portfolio</NavLink>
          </li>
          <li>
            <NavLink to="/Degrees">Degrees</NavLink>
          </li>
          <li>
            <NavLink to="/Contact">Contact Me</NavLink>
          </li>
        </ul>
      </div>
    </div>
  );
}
