import { Component } from "react";
import $ from "jquery";
export default class Functions extends Component {
  componentDidMount() {
    const hamburger_menu = document.querySelector(".hamburger-menu");
    const navbar_container = document.querySelector(".navbar-container");
    const navbar_links = document.querySelector(".navbar-links");
    const navbar_overlay = document.querySelector(".navbar-overlay");
    const navbar_inner = document.querySelector(".navbar-inner");
    const fix_distance = document.querySelector(".fix-distance");
    hamburger_menu.addEventListener("click", () => {
      navbar_container.classList.toggle("active");
      if (window.innerWidth <= 768) {
        // mobile
        if (navbar_container.classList.contains("active")) {
          // menu is open
          navbar_links.style.background = "rgba(0,0,0,.95)";
          navbar_links.style.zIndex = 998;
          navbar_overlay.style.position = "absolute";
          fix_distance.style.marginTop = "0";
          navbar_inner.style.padding = "72px 2rem 20px 2rem";
        } else {
          // menu is Close
          navbar_links.style.zIndex = 2;
          navbar_overlay.style.position = "relative";
          fix_distance.style.marginTop = "-90px";
          navbar_inner.style.padding = "200px 2rem 100px 2rem";
        }
      } else {
        // desktop
        navbar_links.style.zIndex = 2;
        navbar_links.style.background = "rgba(0,0,0,.15)";
        if (navbar_container.classList.contains("active")) {
          // menu is open
          navbar_overlay.style.position = "absolute";
        } else {
          // menu is Close
          navbar_overlay.style.position = "relative";
        }
      }
    });

    let scrollBar = document.getElementById("scrollBar");
    let totalHeight = document.body.scrollHeight - window.innerHeight;
    window.onscroll = function () {
      let progressHeight = (window.pageYOffset / totalHeight) * 100;
      scrollBar.style.height = progressHeight + "%";
    };

    $("document").ready(function () {
      setTimeout(() => {
        document.querySelectorAll(".pre-loader")[0].style.display = "none";
      }, 2500);
    });

    document.addEventListener("contextmenu", (event) => event.preventDefault());
    console.log("Page Loaded :) Right Click Disabled !");
    var navlink0 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[0];
    var navlink1 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[1];
    var navlink2 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[2];
    var navlink3 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[3];
    var navlink4 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[4];
    var navlink5 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[5];
    var navlink6 = document.querySelectorAll(".navbar-links")[0].children[0]
      .children[6];
    navlink0.style.setProperty("--i", "0.05s");
    navlink1.style.setProperty("--i", "0.1s");
    navlink2.style.setProperty("--i", "0.15s");
    navlink3.style.setProperty("--i", "0.2s");
    navlink4.style.setProperty("--i", "0.25s");
    navlink5.style.setProperty("--i", "0.3s");
    navlink6.style.setProperty("--i", "0.35s");
    var loader0 = document.querySelectorAll(".loader")[0].children[0];
    var loader1 = document.querySelectorAll(".loader")[0].children[1];
    var loader2 = document.querySelectorAll(".loader")[0].children[2];
    var loader3 = document.querySelectorAll(".loader")[0].children[3];
    var loader4 = document.querySelectorAll(".loader")[0].children[4];
    var loader5 = document.querySelectorAll(".loader")[0].children[5];
    var loader6 = document.querySelectorAll(".loader")[0].children[6];
    var loader7 = document.querySelectorAll(".loader")[0].children[7];
    loader0.style.setProperty("--i", "0.5");
    loader1.style.setProperty("--i", "1");
    loader2.style.setProperty("--i", "1.5");
    loader3.style.setProperty("--i", "2");
    loader4.style.setProperty("--i", "2.5");
    loader5.style.setProperty("--i", "3");
    loader6.style.setProperty("--i", "3.5");
    loader7.style.setProperty("--i", "4");
  }
  render() {
    return true;
  }
}
