import React from "react";
import { NavLink } from "react-router-dom";
export default function NeonButton(props) {
  return (
    <NavLink type="submit" className="NeonBtn" to={props.to}>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      {props.btnText}
    </NavLink>
  );
}