import React from "react";

export default function ExprienceCard(props) {
  return (
    <div className="edu-item text-left">
      <h4>
        {props.degreeName}
        <br/>
        <a href={props.href}>{props.locationName}</a>
      </h4>
      <span>Year : </span><div className="mh-eduyear">{props.year}</div>
      <br/>
      <span>Responsibility : </span><p>{props.Responsibility}</p>
    </div>
  );
}
