import React, { Component } from "react";
import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Error404 from "./Pages/Error404/Error404";
import Home from "./Pages/HomePage/HomePage";
import AboutMe from "./Pages/AboutMe/AboutMe";
import Degrees from "./Pages/Degrees/Degrees";
import Exprience from "./Pages/Exprience/Exprience";
import Portfolio from "./Pages/Portfolio/Portfolio";
import Skills from "./Pages/Skills/Skills";
import Contact from "./Pages/ContactMe/ContactMe";

export class App extends Component {
  render() {
    return (
      <Router basename="/">
        <Switch>
          <Route path="/" component={Home} exact />
          <Route path="/AboutMe" component={AboutMe} />
          <Route path="/Degrees" component={Degrees} />
          <Route path="/Expriences" component={Exprience} />
          <Route path="/Portfolio" component={Portfolio} />
          <Route path="/Skills" component={Skills} />
          <Route path="/Contact" component={Contact} />
          <Route component={Error404} />
        </Switch>
      </Router>
    );
  }
}

export default App;
