import React from "react";
import Page from "../../Components/Page";
import HexagonPortfolioCard from "../../Components/HexagonPortfolioCard";
import Modal from "../../Components/Modal";
import "./Portfolio.css";
export default function Portfolio() {
  return (
    <Page>
      <div className="portfolios fix-distance">
        <div className="neon-font">
          <b>
            <span>Portfolio</span>
          </b>
        </div>
        <br />
        <br />
        <br />
        <ul id="hexGrid">
          <HexagonPortfolioCard
            imgSrc="./assets/images/GolestanLoginPage.png"
            title="سامانه جامع دانشگاهی گلستان"
            text="React js"
            modalID="#golestanModal"
          />
          <HexagonPortfolioCard
            imgSrc="./assets/images/itilHome.png"
            title="آیتیل"
            text="React js"
            modalID="#itilModal"
          />
          <HexagonPortfolioCard
            imgSrc="./assets/images/BourseTrader.png"
            title="ربات سر خطی بورس"
            text="React js"
            modalID="#boursetraderModal"
          />
          <HexagonPortfolioCard
            imgSrc="./assets/images/iliatebHomePage.png"
            title="ایلیا طب"
            text="wordpress - wooCommerce "
            modalID="#iliatebModal"
          />
          <HexagonPortfolioCard
            imgSrc="./assets/images/telegramBotlogo.png"
            title="انواع ربات تلگرام"
            text="PHP Telegram Bot Api"
            modalID="#galleryModal"
          />
          <HexagonPortfolioCard
            imgSrc="./assets/images/parsblizzard.png"
            title="فروشگاه پارس بلیزارد"
            text="wordpress - wooCommerce"
            modalID="#parsblizzardModal"
          />
          <HexagonPortfolioCard
            imgSrc="./assets/images/JazireHome.png"
            title="خدمات تغذیه و آشپزی جزیره"
            text="Html Css js"
            modalID="#jazirehModal"
          />
        </ul>
        <Modal id="golestanModal" title="Golestan">
          <h3 className="text-left">comprehensive university system</h3>
          <hr />
          <p className="text-left">
            A Website and system for Select Lessons and units of university and
            students .
          </p>
          <br />
          <img
            src="./assets/images/GolestanLoginPage.png"
            alt="GolestanLoginPage"
            width="100%"
          />
          <img
            src="./assets/images/GolestanHomePage.png"
            alt="GolestanHomePage"
            width="50%"
          />
          <img
            src="./assets/images/golestan.gif"
            alt="golestan"
            width="50%"
          />
        </Modal>
        <Modal id="itilModal" title="itilCo">
          <p className="text-left">Technology Compnay for your Projects!</p>
          <br />
          <img
            src="./assets/images/itilHome.png"
            alt="itilHome"
            width="100%"
          />
          <img src="./assets/images/itilPrj.png" alt="itilProjects" width="50%" />
          <img
            src="./assets/images/itillogo.png"
            alt="itillogo"
            width="50%"
          />
        </Modal>
        <Modal id="boursetraderModal" title="Bourse Trader Bot">
          <p className="text-left">
            Demo website for buy and sell in Stock Exchange by Robot ! Faster
            send data to Server and increase your trade speed :)
          </p>
          <br />
          <img
            src="./assets/images/BourseTraderBotPanel.png"
            alt="BourseTraderBotPanel"
            width="100%"
          />
          <img
            src="./assets/images/BourseTraderBotHome.png"
            alt="BourseTraderBotHome"
            width="50%"
          />
          <img
            src="./assets/images/BourseTraderBotLogo.png"
            alt="BourseTraderBotLogo"
            width="50%"
          />
        </Modal>
        <Modal id="iliatebModal" title="IliaTeb">
          <h3 className="text-left">A Cosmetics shop</h3>
          <hr />
          <p className="text-left">Produced by wordpress - wooCommerce</p>
          <br />
          <img
            src="./assets/images/iliatebHomePage.png"
            alt="iliatebHomePage"
            width="100%"
          />
          <img src="./assets/images/iliaTeb.png" alt="iliaTeb" width="30%" />
        </Modal>
        <Modal id="galleryModal" title="Telegram Bot">
          <h3 className="text-left">PHP Telegram Bots</h3>
          <hr />
          <p className="text-left">Create all kinds of telegram robots</p>
          <br />
          <img
            src="./assets/images/telegramBotlogo.png"
            alt="telegramBotlogo"
            width="50%"
          />
          <img
            src="./assets/images/telegramBot.png"
            alt="telegramBot"
            width="50%"
          />
        </Modal>
        <Modal id="parsblizzardModal" title="world of warcraft ">
          <h3 className="text-left">shadowlands</h3>
          <hr />
          <p className="text-left">shop for world of warcraft shadowlands</p>
          <br />
          <img
            src="./assets/images/parsblizzard.png"
            alt="parsblizzard"
            width="100%"
          />
          <img
            src="./assets/images/parsblizzard.ir.png"
            alt="parsblizzardLogo"
            width="50%"
          />
        </Modal>
        <Modal id="jazirehModal" title="Jazire">
          <p className="text-left">Demo Website for kitchen and restaurant</p>
          <br />
          <img
            src="./assets/images/JazireHome.png"
            alt="Jazire"
            width="100%"
          />
        </Modal>
      </div>
    </Page>
  );
}
