import React from "react";

export default function ProgressBar(props) {
  return (
    <div className="techskills set-ele-middle flex-column w-100">
      <div className="progress-header row">
        <div className="col-sm-6 text-left">
          <h6>{props.progressName}</h6>
        </div>
        <div className="col-sm-6 text-right">
          <h5>{props.progressPersent}</h5>
        </div>
      </div>
      <div className="progress-wrap">
        <div id="progress-meter" className="progress-meter animate">
          {/*nostripes */}
          <span style={{ width: props.progressPersent }}>
            <span></span>
          </span>
        </div>
      </div>
    </div>
  );
}
