import React from "react";
import Page from "../../Components/Page";
import Icon from "@mdi/react";
import "./ContactMe.css";

import {
  mdiMapMarker,
  mdiPhone,
  mdiEmail
} from "@mdi/js";
export default function ContactMe() {
  return (
    <Page>
      <div className="contact fix-distance">
        <div className="neon-font mb-5">
          <b>
            <span>ContactMe</span>
          </b>
        </div>

        <div className="col-sm-12">
          <div className="container">
            <div className="row">
              <div className="col-sm-12 col-md-6">
                <div className="col-sm-12 xs-no-padding">
                  <div className="address-item">
                  <Icon path={mdiMapMarker} size={1} />
                    <div>
                      <h4>Address</h4>
                      <address>
                        iran , Tehran , Damavand St , <br />
                        Sedighian Ave.
                      </address>
                    </div>
                  </div>
                </div>
                <div className="col-sm-12 xs-no-padding">
                  <div className="address-item">
                  <Icon path={mdiEmail} size={1} />
                    <div>
                      <h4>Email</h4>
                      <span>AmirGoodarzi.Web@Gmail.com</span>
                      <br />
                      <span>Sektowr@Gmail.com</span>
                    </div>
                  </div>
                </div>
                <div className="col-sm-12 xs-no-padding">
                  <div className="address-item">
                  <Icon path={mdiPhone} size={1} />
                    <div>
                      <h4>Phone</h4>
                      <a href="callto:(880)-8976-987">+98 905 632 5077</a>
                      <br />
                      <a href="callto:(880)-8976-987">+98 916 794 3707</a>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-sm-12 col-md-6 set-ele-middle">
                <form id="contactForm">
                  <div id="msgSubmit" className="text-center hidden"></div>
                  <div className="row">
                    <div className="col-sm-12">
                      <input
                        name="name"
                        className="contact-name form-control"
                        id="name"
                        type="text"
                        placeholder="First Name"
                        required
                      />
                    </div>

                    <div className="col-sm-12">
                      <input
                        name="Lname"
                        className="contact-email form-control"
                        id="L_name"
                        type="text"
                        placeholder="Last Name"
                        required
                      />
                    </div>

                    <div className="col-sm-12">
                      <input
                        name="email"
                        className="contact-subject form-control"
                        id="email"
                        type="email"
                        placeholder="Your Email"
                        required
                      />
                    </div>

                    <div className="col-sm-12">
                      <textarea
                        className="contact-message"
                        id="message"
                        rows="6"
                        placeholder="Your Message"
                        required
                      ></textarea>
                    </div>
                    <div className="btn-form col-sm-12">
                      <button
                        type="submit"
                        className="NeonBtn btn-send-message"
                        id="form-submit"
                      >
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        Send Message
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
}
